import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { titleChange, teamBreakup, detail, dischargeMembers } from '../../api/team';
import styled from "styled-components";
import { useParams } from 'react-router-dom';
import { store } from '../..';

export default function TeamSettings() {

		// profile_id, team_id  
		const myProfileId = store.getState().user.profileid;

		const [title, setTitle] = useState('');
		// const [membersName, setMemberName] = useState('');
		// const [membersEmail, setMembersEmail] = useState('');
		const [members, setMembers] = useState('');

		const teamId = useParams().id;

		const changeName = (e) => {
			setTitle(e.target.value);
		}

		const toChangeTitle = (e) => {
			e.preventDefault();
			
			setTitle(''); // 
			titleChange(
			myProfileId, teamId, title,
			(response) => {
				console.log(response);
			},
			(error) => {
				console.log('', error);
			})
		}

		//  
		const TeamDelete = (e) => {
			teamBreakup(
				teamId,
				(response) => {
					console.log(response);
				},
				(error) => {
					console.log("", error);
				}
			)
		}	

		
		//    
		useEffect(() => {
			detail(
				teamId,
				(response) => {
					setMembers(response.data[teamId]);
					console.log(response.data)
				},
				(error) => {
					console.log('', error);
				}
				)}, [])
		
		//    
		const membersInfo = () => {
			//  
			const outMembers = (profileId) => {
				//   (api)
				dischargeMembers(
					teamId, profileId,
					(response) => {
						console.log(response);
					},
					(error) => {
						console.log('', error);
					}
				)
			}

			const result = [];
			for (let i=1; i < members.length; i++) {
				const profileId = members[i].user.profile.profile_id;
				console.log(profileId);
				result.push(
				 <div key={i}>
					 { `${members[i].user.name}` }
					 { `${members[i].user.email}` }
					 <button onClick={() => outMembers(profileId)}></button>
				 </div> 
				)
				
			}
			return result;
		};

    return (
				<Container>
					<h3>  </h3>

					<hr />

					<div>
						<h3>  </h3>
						<input type="text" onChange={changeName}/>						
					</div>
					<button onClick={toChangeTitle}></button>
					
					<hr />

					<h3>  </h3>
					{/* {membersName}, {email} */}
					{membersInfo()}
					{/* {console.log(membersInfo)} */}

					{/* {membersName ? membersName.map((el, key) => {
						return (
							<div>
								<SettingsMembers
									key={key+1}
									name={el}
								/>
							</div>
						)
					}): null} */}
					<hr />
					<button onClick={TeamDelete}> </button>

        </Container>
    )
}
const Container = styled.div`
	position: absolute;
	width: 60%;
	height: 60%;

	top: 35%;
	left: 50%;
	transform:translate(-50%, -50%);
`

const SettingsMembers = styled.div`
`