import React, { useEffect, useState } from 'react';
import { detail, bringTeamMembers, editTeamContent, bringMembersImg } from '../../api/team';
import styled from "styled-components";
import { useParams } from 'react-router-dom';
import TeamFeedContainer from './TeamFeedContainer';
import TeamMemberInfo from './TeamMemberInfo'
import MembersImg from './MembersImg';
import { Link } from "react-router-dom";
import { store } from '../..';

export default function TeamDetail(props) {
    const [teamTitle, setTeamTitle] = useState('');
    const [teamContent, setTeamContent] = useState('');
    const [teamMembers, setTeamMembers] = useState('');
    const [checkId, setCheckId] = useState(''); //   id
    const [showInput, setShowInput] = useState(false);
		const [editInput, setEditInput] = useState('');
		const membersImgsList = [];

    const teamId = useParams().id;
		const profileId = store.getState().user.profileid;

		//   Leader 
		const LeaderSettings = styled.div`
				${() => {
						return checkId == profileId ? "display:flex;" : "display:none;";
				}}
		`

    //     
    useEffect(() => {
        detail(teamId,
            (response) => {
                setTeamContent(response.data[teamId][0].team.content);
                setTeamTitle(response.data[teamId][0].team.title);
                setCheckId(response.data[teamId][0].user.profile.profile_id);
								// console.log(response.data[teamId][0].user.profile.profile_id);
            },
            (error) => {
                console.log(" .", (error));
                                console.log(teamId)
            });
        }, []);

		//   
		// useEffect(() => {
		// 	bringMembersImg(
		// 		88,
		// 		(res) => {
		// 			const url = window.URL.createObjectURL(
		// 				new Blob([res.data], { type: res.headers['content-type'] })
		// 			)
		// 			membersImgsList.push(url)
		// 		},
		// 		(error) => console.log(error)
		// 	)
		// })

    //   
    useEffect(() => {
        bringTeamMembers(teamId,
            (response) => {
                setTeamMembers(response.data);
            },
            (error) => {
                console.log(" .", (error));
            });
        },[]);

    //   
			// Leader   
		const TextBox = styled.div`
				${() => {
					return checkId == profileId ? "display : flex;" : "display: none;";
				}}
		`
		  //   Input
		const inputChange = (e) => {
			setEditInput(e.target.value);
		}
		  //    Btn
		const editSubmit = (params) => {
			editTeamContent(
				teamId, profileId, params,
				(response) => {
					console.log(response);
					window.location.replace(`/auth/team/${teamId}`)
				},
				(error) => {
					console.log(error);
					console.log(teamId, profileId, params)
				}
			)
		}



    return (

    <TeamDetailBox>
			<TeamDetailHeaders>
				<LeaderSettings>
					<Link to={{pathname:`/auth/team/${teamId}/settings`}}>
							<button> </button>
					</Link>
				</LeaderSettings>

				{/*  Title */}
				<TheTeamTitle>
						{teamTitle}
				</TheTeamTitle>
			</TeamDetailHeaders>

			<TeamDetailGrid>
					{/*   */}
					<TeamFeedContainer>
					</TeamFeedContainer>

					{/*   */}
					<TeamInfoContainer>
							<TeamInfoTitle> </TeamInfoTitle>
							{/* /  */}
							<ImgBox>
									{teamMembers ? teamMembers.map((el, key) => {
											return (
													<TeamMemberInfo name={el.name} email={el.email} key={key}/>
											)
									}) : null}

								{/* {membersImgsList}	 */}


									{/* <BringTeamMembersImg 
									src="https://w.namu.la/s/38cf17d29ddeab5a69f6de682176bbd6b8f71285f5adc1d5465c910f8d7651e8f82db2bdba9e25f1d29affdedb9ddc04edeadc4e7f539ce975eaad093a2b8c68adc73e19b58ff0f4cce679d2f2bb15e273bdcaa5e3db26bd4464e1707b67c69a"
									></BringTeamMembersImg>
									<BringTeamMembersImg 
									src="https://w.namu.la/s/38cf17d29ddeab5a69f6de682176bbd6b8f71285f5adc1d5465c910f8d7651e8f82db2bdba9e25f1d29affdedb9ddc04edeadc4e7f539ce975eaad093a2b8c68adc73e19b58ff0f4cce679d2f2bb15e273bdcaa5e3db26bd4464e1707b67c69a"
									></BringTeamMembersImg>
									<BringTeamMembersImg 
									src="https://w.namu.la/s/38cf17d29ddeab5a69f6de682176bbd6b8f71285f5adc1d5465c910f8d7651e8f82db2bdba9e25f1d29affdedb9ddc04edeadc4e7f539ce975eaad093a2b8c68adc73e19b58ff0f4cce679d2f2bb15e273bdcaa5e3db26bd4464e1707b67c69a"
									></BringTeamMembersImg>
									<BringTeamMembersImg 
									src="https://w.namu.la/s/38cf17d29ddeab5a69f6de682176bbd6b8f71285f5adc1d5465c910f8d7651e8f82db2bdba9e25f1d29affdedb9ddc04edeadc4e7f539ce975eaad093a2b8c68adc73e19b58ff0f4cce679d2f2bb15e273bdcaa5e3db26bd4464e1707b67c69a"
									></BringTeamMembersImg> */}
							</ImgBox>
							{/*  Content */}
							<TeamInfoTextBox>

									{teamContent}
									<TextBox>
										<button onClick={() => {setShowInput(!showInput)}}> </button>
									</TextBox>

										{ showInput && 
											<>
												<input type="text" onChange={inputChange} />
												<button onClick={() => {editSubmit(editInput)}}></button>
											</>
										}
										
							</TeamInfoTextBox>

							{/*    */}
							<TeamInfoHashtag>
									# # #
							</TeamInfoHashtag>

					</TeamInfoContainer>
			</TeamDetailGrid>
    </TeamDetailBox>

    )
}
const TeamDetailHeaders = styled.div`
	display: flex;
`
const TeamDetailBox = styled.div`
`
const TheTeamTitle = styled.h3`
    position: relative;
    margin-top: 2%;
    left: 5%;
`
const TeamDetailGrid = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50%, -50%);
    display: flex;
    grid-gap: 14%;
    justify-content: center;
    align-items: flex-start;
    height: 60vh;
    width: 80vw;

    // grid-template-columns: 45vw 20vw;
    // grid-template-rows: 70vh 40vh;
`

//  
const TeamInfoContainer = styled.div`
    border: 1px solid;
    // text-align: center;
    border-radius: 5px;

    display: flex;
    flex-direction: column;
    height: 40vh;
    width: 20vw;
    overflow: scroll;

`
const TeamInfoTitle = styled.h3`
    position: relative;
    margin-top: 5%;
    left: 5%;
    font-weight: bold;
`
const BringTeamMembersImg = styled.img`
    width: 50px;
    height: 50px;
`
const ImgBox = styled.div`
    position: relative;
    left: 5%;
    margin-top: 5%;
    width:90%;
`
const TeamInfoTextBox = styled.div`
    position: relative;
    margin-top: 5%;
    left: 5%;
    width: 90%;
    word-break:break-all;
`
const TeamInfoHashtag = styled.div`
    position: relative;
    margin-top: 5%;
    left: 5%;
    width: 90%;
    word-break:break-all;
`